<template>
    <div class="grid-container">
        <h2 id="post-content">{{content}}</h2>
        <div>
            <input v-if="editBtnTgl" type="text" v-model="updatedPost"/>
            <button v-if="editBtnTgl" class="postBtn" @click="updatePost">Update</button>
        </div>

        <div class="grid-container-inner">
            <ul>
                <li><button class="postBtn" @click="prepareUpdate">Update</button></li>
                <li><button class="postBtn" @click="deletePost">Delete</button></li>
            </ul>
            
        </div>
        
    </div>
</template>

<script>
    import axios from "axios"
    export default {
        name: "Posts",
        props: {
            id: Number,
            content: String,
        },
        data() {
            return {
                editBtnTgl: false,
                updatedPost: "",
            }
        },
        methods: {
            prepareUpdate() {
                this.editBtnTgl = true
            },
            updatePost() {
                axios.request({
                    url: 'https://viadellapizza.ml/api/posts',
                    method: 'PATCH',
                    headers : {
                        'Content-Type': 'application/json'
                    },
                    data : {
                        "id" : this.id,
                        "content" : this.updatedPost
                    }
                }).then((response) => {
                    console.log(response)
                }).catch((error) => {
                    console.error("There was an error: " +error);
                })
            },
            deletePost() {
                axios.request({
                    url: 'https://viadellapizza.ml/api/posts',
                    method: 'DELETE',
                    headers : {
                        'Content-Type': 'application/json'
                    },
                    data : {
                        "id" : this.id,
                    }
                }).then((response) => {
                    console.log(response)
                }).catch((error) => {
                    console.error("There was an error: " +error);
                })
            },
        }
    }
</script>

<style scoped>
    .grid-container {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        height: 200px;
        width: 60vw;
        border: 1px solid white;
        margin-left: auto;
        margin-right: auto;
        margin-top: 10px;
        margin-bottom: 10px;
        justify-content: center;
        align-content: center;
    
    }
    grid-container > div {
        justify-self: center;
        align-self: center;
    }
    .grid-container-inner {
        display: flex;
        flex-direction: column;
    }
    li {
        list-style: none;
    }
    #post-content {
        color: rgb(145, 124, 124);
    }
    .postBtn {
	box-shadow:inset 0px 39px 0px -24px #e67a73;
	background-color:#e4685d;
	border-radius:4px;
	border:1px solid #ffffff;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	padding:6px 15px;
	text-decoration:none;
	text-shadow:0px 1px 0px #b23e35;
    margin-top: 5px;
}
.postBtn:hover {
	background-color:#eb675e;
}
.postBtn:active {
	position:relative;
	top:1px;
}
</style>