<template>
    <body>
        <div>
            <h1>Blog Posting Website</h1>
            <input type="text" v-model="postContent">
            <div>
            <button class="btn" @click="createPost">Post</button>
        </div>
        <div>
            <Posts v-for="post in allPostData"
                        :key="post.commentId"
                        :id="post.id"
                        :content="post.content"/>
        </div>
        </div>
    </body>
</template>

<script>
import axios from "axios"
import Posts from "../components/Posts.vue";
    
    export default {
        name: "Home",
        components: {
            Posts
        },
        data: () => {
            return {
                postContent: "",
                allPostData: [],
                newPostObj: {},

            }
        },
        methods: {
            retrievePost() {
                axios.request({
                    url: 'https://viadellapizza.ml/api/posts',
                    method: 'GET',

                }).then((response) => {
                    console.log(response)
                    this.allPostData = response.data;
                    this.allPostData.sort(function(x,y){
                    return new Date(y.createdAt) - new Date(x.createdAt);
                    })

                }).catch((error) => {
                    console.error("There was an error: " +error);
                })
            },
            createPost() {
                axios.request({
                    url: 'https://viadellapizza.ml/api/posts',
                    method: 'POST',
                    headers : {
                        'Content-Type': 'application/json'
                    },
                    data : {
                        "content" : this.postContent,
                    }
                }).then((response) => {
                    console.log(response)
                    this.newPostObj = {
                        content : response.data.content
                    }
                    this.allPostData.unshift(this.newPostObj);
                    this.content = "";
                    this.retrievePost()

                }).catch((error) => {
                    console.error("There was an error: " +error);
                })
            },
        },
        mounted() {
            this.retrievePost();
        },
    }
</script>

<style scoped>
    
    input {
        margin-top: 10px;
        width: 20vw;
        height: 5vh;
    }

    .btn {
        box-shadow:inset 0px 1px 0px 0px #ffffff;
        background:linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
        background-color:#ffffff;
        border-radius:6px;
        border:1px solid #dcdcdc;
        display:inline-block;
        cursor:pointer;
        color:#666666;
        font-size:15px;
        font-weight:bold;
        padding:6px 24px;
        text-decoration:none;
        text-shadow:0px 1px 0px #ffffff;
    }
    .btn:hover {
        background:linear-gradient(to bottom, #f6f6f6 5%, #ffffff 100%);
        background-color:#f6f6f6;
    }
    .btn:active {
        position:relative;
        top:1px;
    }
</style>

